.stars {
  display: flex;

  @for $starCount from 1 through 5 {
    &--#{$starCount} .stars__star:nth-child(-n + #{$starCount}) {
      background-image: url(/images/star-active.svg);
    }
  }

  &__star {
    height: 16px;
    width: 16px;
    background: center no-repeat url(/images/star.svg);

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
}
